import { Component } from '@angular/core';
import { IonicPage, NavParams, AlertController, NavController, LoadingController, ToastController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-deposit-money',
  segment: 'deposit-money'
})

@Component({
  selector: 'page-deposit-money',
  templateUrl: 'deposit-money.html'
})

export class DepositMoneyPage {
  general: any;
  showUp: boolean = false;
  cryptocurrency: any;
  relationship: any;
  bank: any;
  amount: number;
  reference: any;
  walletBalance: any;
  showpara: boolean = false;
  hide: boolean = true;
  currencyName: any;
  body: any;
  iPaymentId: any;
  PayNow_link: any;
  iApiTransactionId: any;
  EFT: any;
  CreditCard: any;
  Crypto: any;
  fees: number;
  total: number;
  pay_link;
  currencysymbol: any;
  constructor(public navCtrl: NavController, public translate: TranslateService, public navparams: NavParams, public user: UserProvider, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.amount = this.navparams.get('amount');
    this.body = this.navparams.get('data');
    console.log(this.body);
    this.EFT = this.body.EFT;
    this.reference = this.body.reference;
    this.fees = this.amount * 0.029;
    this.total = +this.amount + +this.fees;
    this.CreditCard = this.body.CreditCard;
    this.pay_link = this.body.CreditCard.data.PayNow_link;
    console.log(this.pay_link, this.CreditCard, this.fees, this.total);

    console.log(this.amount, this.EFT, this.reference);


    this.user.jurisdictionlistBank().subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        this.bank = data2.data[0].bank;
        console.log(this.bank);


      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.navCtrl.setRoot('page-general_error');
        }, 1000);
      },
    )


    var memberType = JSON.parse(localStorage.getItem('currentUser'));
    this.relationship = 'eft';
    this.general = memberType.general.MemberType;
    console.log(this.general);
    this.general = 1;
    if (this.general > 0) {
      this.showUp = true;


    } else {
      this.showUp = false;


      this.translate.get('deposit-money-page').subscribe((translated: string) => {
        var toast1 = this.translate.instant('deposit-money-page.toast1');

        let toast = this.toastCtrl.create({
          message: toast1,
          duration: 3000,
          position: 'top'
        });

        toast.onDidDismiss(() => {
          console.log('Dismissed toast');

        });

        toast.present();

      })



    }

    this.updateBalance();

  }

  home1(){
    this.navCtrl.setRoot('page-home');
  }

  mail(){
    let alert = this.alertCtrl.create({
      title: 'Deposit',
      message: 'The deposit banking details will be sent to your mailbox.',
      buttons: [
        {
          text: 'Ok',
          handler: () => {
            this.navCtrl.setRoot('page-my-wallet');
            
          }
        }
      ]
    });
    alert.present();
  }


  // update balance 

  updateBalance() {


    this.user.showBalance().subscribe(
      data => {
        console.log(data);

        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        console.log(data2.data);
        if (data2.data.length === 0) {
          this.walletBalance = 0.00;
          this.currencyName = "ZAR";
          this.currencysymbol = "R";
          if (this.walletBalance < this.amount) {
            this.showpara = true;
            this.hide = false;
          }


        } else {
          this.walletBalance = data2.data[0].Available;
          this.currencyName = data2.data[0].Currency;
          this.currencysymbol = data2.data[0].LocalCurrencySymbol;

          if (this.walletBalance < this.amount) {

            this.showpara = true;
            this.hide = false;

          }

        }




      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.navCtrl.setRoot('page-general_error');
        }, 1000);
      },
    )

  }


  // segment change 

  segmentChanged(event) {
    console.log(event);
  }


  // next go to add amount page
  nextpage(bankname, code) {
    console.log(bankname, code);

    var memberType = JSON.parse(localStorage.getItem('currentUser'));
    this.general = memberType.general.MemberType;
    console.log(this.general);
    this.general = 1;
    if (this.general > 0) {

      this.navCtrl.push('page-add-amount', { 'bankname': bankname });

    } else {

      this.translate.get('deposit-money-page').subscribe((translated: string) => {
        var toast2 = this.translate.instant('deposit-money-page.toast2');
        let toast = this.toastCtrl.create({
          message: toast2,
          duration: 3000,
          position: 'top'
        });

        toast.onDidDismiss(() => {
          console.log('Dismissed toast');

        });

        toast.present();

      })


    }


  }


  // next go to add amount page
  cryptopayment(cyrpto) {
    this.cryptocurrency = cyrpto;
    var memberType = JSON.parse(localStorage.getItem('currentUser'));
    this.general = memberType.general.MemberType;
    console.log(this.general);
    this.general = 1;
    if (this.general > 0) {


      this.navCtrl.push('page-add-cyrpto-amount', { 'crypto': this.cryptocurrency });

    } else {

      this.translate.get('deposit-money-page.alert').subscribe((translated: string) => {

        var title = this.translate.instant('deposit-money-page.alert.title');
        var subTitle = this.translate.instant('deposit-money-page.alert.subTitle');
        var dismiss_btn = this.translate.instant('deposit-money-page.alert.dismiss_btn');

        let alert = this.alertCtrl.create({
          title: title,
          subTitle: subTitle,
          buttons: [dismiss_btn]
        });
        alert.present();

      })

    }


  }


  // onchange select 

  onChange1(event) {

    console.log(event);
    this.navCtrl.push('page-add-cyrpto-amount', { 'crypto': event });

  }


  close() {
    this.navCtrl.setRoot('page-my-wallet');
  }

}



